class XHR {
  constructor ({url, method = 'GET', type = 'arraybuffer', timeout = 10000, data = {}} = {}, context = {}) {
    return new Promise((resolve, reject) => {
      let R = new window.XMLHttpRequest()
      let _method = method.toUpperCase()
      let _data = []
      const result = {
        headers: {},
        status: 0,
        statusText: '',
        connectTime: 0,
        speed: 0,
        url: url
      }
      if (type) {
        R.responseType = type
      }
      if (timeout) {
        R.timeout = timeout
      }
      for (let k in data) {
        _data.push(`k=${data[k]}`)
      }
      R.onload = () => {
        if (R.status === 200 || R.status === 206) {
          resolve({context, res: R})
        } else {
          reject({context, res: R, type: 'error' })
        }
      }
      R.onerror = (error) => {
        reject({context, res: R, type: 'error' })
      }
      R.ontimeout = (error) => {
        reject({context, res: R, type: 'error' })
      }
      R.onabort = () => {
        reject({context, res: R, type: 'error' })
      }

      if (_method === 'GET') {
        // R.withCredentials = true;
        R.open(_method, `${url}`)
        R.send()
      } else if (_method === 'post') {
        R.open(_method, url)
        R.setRequestHeader('Content-type', 'application/x-www-form-urlencoded')
        R.send(_data.join('&'))
      } else {
        throw new Error(`xhr ${_method} is not supported`)
      }
    })
  }
}

export default XHR
